export default {
	data() {
		return {
			CustomBar: this.CustomBar,
			InputBottom:0,
			downOption: {
				auto: false //是否在初始化后,自动执行下拉回调callback; 默认true
			},
			upOption:{
				auto:false,
			},
			conditions:{
				status:'0',
				isHead:'1'
			},
			p_list:[],
			loading:true,
			conditions_modal:false,
			status_radio:'0',
			switch_is_head:true,
			recId:''
		};
	},
	computed:{
		c_CustomBar(){
			return this.CustomBar + 42
		},
		no_data(){
			return this.p_list.length<=0 && !this.loading
		},
		check_length(){
			let num = 0
			this.p_list.forEach(item =>{
				if(item.checked){
					num++
				}
			})
			return num
		}
	},
	onLoad(option) {
		this.recId = option.recId || ''
		this.init()
	},
	methods:{
		init(){
			this.$http.get('personwx.personinfolist/1.0/',{
				status:this.conditions.status,
				isHead:this.conditions.isHead
			}).then(res =>{
				console.log(res)
				this.p_list = res.data.data
				this.p_list.forEach(item =>{
					this.$set(item,'checked',false)
				})
				this.loading = false
			})
		},
		downCallback(mescroll) {
			// 这里加载你想下拉刷新的数据, 比如刷新轮播数据
			// loadSwiper();
			// 下拉刷新的回调,默认重置上拉加载列表为第一页 (自动执行 mescroll.num=1, 再触发upCallback方法 )
			mescroll.resetUpScroll()
		},
		/*上拉加载的回调: mescroll携带page的参数, 其中num:当前页 从1开始, size:每页数据条数,默认10 */
		upCallback(mescroll) {
			//联网加载数据
			setTimeout(() =>{
				mescroll.endErr()
			},1000)
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		},
		checkboxChange(e){
			var items = this.p_list,
				values = e.detail.value;
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				const item = items[i]
				if(values.includes(item.id)){
					this.$set(item,'checked',true)
				}else{
					this.$set(item,'checked',false)
				}
			}
		},
		goto_detail(item){
			uni.navigateTo({
				url: `/pages/auditDetail/auditDetail?id=${item.id}`
			});
		},
		hideModal(){
			this.conditions_modal = false
		},
		RadioChange(e){
			this.status_radio = e.detail.value
		},
		SwitchHead(e){
			this.switch_is_head = e.detail.value
		},
		res_conditions(){
			this.status_radio = '0',
			this.switch_is_head = false
		},
		sub_search(){
			this.conditions.status = this.status_radio
			this.conditions.isHead = this.switch_is_head?'1':'0'
			this.loading = true
			this.conditions_modal = false
			this.init()
		},
		formSubmit(e){
			console.log('form发生了submit事件，携带数据为：' , e)
			let formId = e.detail.formId,
				Ids=[],
				openIds=[];
			this.p_list.forEach(item =>{
				if(item.checked){
					Ids.push(item.id)
					openIds.push(item.openId)
				}
			})
			let params = {
				Ids:Ids.join(),
				openIds:openIds.join(),
				recId:this.recId,
				formId:formId,
				path:'pages/tabbar/task/task',
			}
			console.log(params)
			this.$http.post('personwx.chooseperson/1.0/',params).then(res =>{
				console.log(res)
				if(res.data.code == '0'){
					uni.navigateBack({
					    delta: 1
					});
				}
			})
		},
		avatarUrl(avatarUrl){
			return `url(${avatarUrl})`
		},
	}
}
